// TgGroupsAppMain.js
// ENL Fi Dev / RedFoxFinn
// Telegram-group listings web-application main file

import React from 'react';
import { connect } from 'react-redux';
import Clock from 'react-live-clock';
import { Container, Row, Col } from 'react-grid-system';
import ServerConnection from '../../services/ServerConnection.js';

import TgGroupsAppLanguageSelection from './TgGroupsAppLanguageSelection.js';

const mapStateToProps = (state) => {
  return {
    appState: state.appState,
    groupState: state.groupState
  };
};
function textByLanguageSetting(siteLanguage) {
  switch (siteLanguage) {
    case 'fi':
      return ({
        header: 'ENL Fi Offtopic -Telegram-ryhmät',
        time: 'UTC-aika : ',
        addGroupHeader: 'Lisää uusi _offtopic_ -ryhmä:',
        addGroupNamePlaceholder: 'ryhmän nimi',
        addGroupSheriffPlaceholder: 'ryhmän ylläpitäjä',
        addGroupLinkPlaceholder: 't.me/ -linkki',
        addGroupInfoPlaceholder: 'kuvaus',
        addGroupVerifyPlaceholder: 'tunnistaudu',
        groupListHeader: 'Listatut Offtopic-ryhmät:',
        currentSiteLanguage: 'Valittu kieli: ',
        name: 'Ryhmän nimi:',
        sheriff: 'Ylläpito:',
        info: 'Kuvaus:',
        join: 'Liity'
      });
    case 'en':
      return ({
        header: 'ENL Fi Offtopic -Telegram-groups',
        time: 'UTC-time : ',
        addGroupHeader: 'Add new _offtopic_ -group:',
        addGroupNamePlaceholder: 'group name',
        addGroupSheriffPlaceholder: 'group owner/admin',
        addGroupLinkPlaceholder: 't.me/ -link',
        addGroupInfoPlaceholder: 'description',
        addGroupVerifyPlaceholder: 'verify (yourself)',
        addGroupSubmit: 'Submit',
        groupListHeader: 'Listed Offtopic-groups:',
        currentSiteLanguage: 'Selected language: ',
        name: 'Group name:',
        sheriff: 'Owner/admin:',
        info: 'Description:',
        join: 'Join'
      });
    default:
      return ({
        header: 'ENL Fi Offtopic -Telegram-ryhmät',
        time: 'UTC-aika : ',
        addGroupHeader: 'Lisää uusi _offtopic_ -ryhmä:',
        addGroupNamePlaceholder: 'ryhmän nimi',
        addGroupSheriffPlaceholder: 'ryhmän ylläpitäjä',
        addGroupLinkPlaceholder: 't.me/ -linkki',
        addGroupInfoPlaceholder: 'kuvaus',
        addGroupVerifyPlaceholder: 'tunnistaudu',
        addGroupSubmit: 'Talleta',
        groupListHeader: 'Listatut Offtopic-ryhmät:',
        currentSiteLanguage: 'Valittu kieli: ',
        name: 'Ryhmän nimi:',
        sheriff: 'Ylläpito:',
        info: 'Kuvaus:',
        join: 'Liity'
      });
  }
}
function renderGroups(tgGroups, join) {
  const current = new Date().valueOf();
  return (
    tgGroups.map(tgGroup => {
      return (
        <Row justify='around' align='center'>
          <p>{tgGroup.name}</p>
          <p>{tgGroup.sheriff}</p>
          <p>{tgGroup.info}</p>
          <a type='button' href={tgGroup.link} title={tgGroup.link} aria-disabled={tgGroup.linkExpDateTime <= current}>{join}</a>
        </Row>
      )
    })
  )
}

class TgGroupsAppMain extends React.Component {
  componentDidMount() {
    ServerConnection.getGroups().then(response => { this.props.dispatch({type: 'SET_GROUPS', groups: response.data})});
  };
  
  controlNameField(event) {
    event.preventDefault();
    this.props.dispatch({type: 'SET_NEW_NAME', newGroupName: event.target.value});
  }
  controlSheriffField(event) {
    event.preventDefault();
    this.props.dispatch({type: 'SET_NEW_SHERIFF', newGroupSheriff: event.target.value});
  }
  controlLinkField(event) {
    event.preventDefault();
    this.props.dispatch({type: 'SET_NEW_LINK', newGroupLink: event.target.value});
  }
  controlInfoField(event) {
    event.preventDefault();
    this.props.dispatch({type: 'SET_NEW_INFO', newGroupInfo: event.target.value});
  }
  controlVerificationField(event) {
    event.preventDefault();
    this.props.dispatch({type: 'SET_VERIFICATION', verification: event.target.value});
  }
  
  submitGroup(event) {
    event.preventDefault();
    const current = new Date();
    const linkDateTime = current.valueOf();
    const linkExpDateTime = current.setFullYear(current.getFullYear() +1);
    
    ServerConnection.postGroup(
      this.props.groupState.newGroupName,
      this.props.groupState.newGroupSheriff,
      this.props.groupState.newGroupLink,
      this.props.groupState.newGroupInfo,
      linkDateTime,
      linkExpDateTime
    ).then(response => {
      this.props.dispatch({type: 'NEW_GROUP_SUBMIT', groups: response.data});
    });
  }
  
  render() {
    
    let appState = this.props.appState;
    let groupState = this.props.groupState;
    let time = <Clock format={'HH:mm:ss'} ticking={true} timezone={'Etc/UTC'}/>;
    let texts = textByLanguageSetting(this.props.appState.siteLanguage);
    let siteLanguage = appState.siteLanguage;
    
    return (
      <div>
        <Container fluid>
          <TgGroupsAppLanguageSelection />
          <Row justify='around' align='center'>
            <h2>{texts.header}</h2>
            <p>{texts.time}{time}</p>
            <p>{texts.currentSiteLanguage}{siteLanguage}</p>
          </Row>
          <Row justify='around' align='center'>
            <h3>{texts.addGroupHeader}</h3>
            <p> </p>
            <p> </p>
          </Row>
          <Row justify='around' align='center'>
            <form onSubmit={event => this.submitGroup(event)}>
              <input type='text' required placeholder={texts.addGroupNamePlaceholder}
                     value={groupState.newGroupName} onChange={event => this.controlNameField(event)}/>
              <br/>
              <br/>
              <input type='text' required placeholder={texts.addGroupSheriffPlaceholder}
                     value={groupState.newGroupSheriff} onChange={event => this.controlSheriffField(event)}/>
              <br/>
              <br/>
              <input type='text' required placeholder={texts.addGroupLinkPlaceholder}
                     value={groupState.newGroupLink} onChange={event => this.controlLinkField(event)}/>
              <br/>
              <br/>
              <input type='text' placeholder={texts.addGroupInfoPlaceholder}
                     value={groupState.newGroupInfo} onChange={event => this.controlInfoField(event)}/>
              <br/>
              <br/>
              <input type='password' required minLength='8' placeholder={texts.addGroupVerifyPlaceholder}
                     value={groupState.verification} onChange={event => this.controlVerificationField(event)}/>
              <br/>
              <br/>
              <input type='submit' value={texts.addGroupSubmit}/>
            </form>
            <p> </p>
            <p> </p>
          </Row>
          <Row justify='around' align='center'>
            <h4>{texts.name}</h4>
            <h4>{texts.sheriff}</h4>
            <h4>{texts.info}</h4>
            <h4> </h4>
          </Row>
          {renderGroups(groupState.groups, texts.join)}
        </Container>
      </div>
    )
  }
}

export default connect(mapStateToProps)(TgGroupsAppMain)


