// TgGroupsGroupReducer.js
// ENL Fi Dev / RedFoxFinn
// Redux-reducer for Telegram-group listings web-application

const getInitialState = () => ({
  groups: [],
  newGroupName: '',
  newGroupSheriff: '',
  newGroupLink: '',
  newGroupInfo: '',
  verification: ''
});

const groupReducer = (state = getInitialState(), action) => {
  switch (action.type) {
    case 'SET_GROUPS':
      return {...state, groups: action.groups};
    case 'SET_NEW_NAME':
      return {...state, newGroupName: action.newGroupName};
    case 'SET_NEW_SHERIFF':
      return {...state, newGroupSheriff: action.newGroupSheriff};
    case 'SET_NEW_LINK':
      return {...state, newGroupLink: action.newGroupLink};
    case 'SET_NEW_INFO':
      return {...state, newGroupInfo: action.newGroupInfo};
    case 'SET_NEW_LINK_DATETIME':
      return {...state, newGroupLinkDateTime: action.newGroupLinkDateTime};
    case 'SET_NEW_LINK_EXP_DATETIME':
      return {...state, newGroupLinkExpDateTime: action.newGroupLinkExpDateTime};
    case 'SET_VERIFICATION':
      return {...state, verification: action.verification};
    case 'NEW_GROUP_SUBMIT':
      return {groups: action.groups, newGroupName: '', newGroupSheriff: '', newGroupInfo: '', newGroupLink: '', verification: ''};
    default:
      return state;
  }
};

export default groupReducer


