

import axios from 'axios';

const url = '/api';

const TgGroupsUrl = `${url}/groups`;

const getGroups = async () => {
  return await axios.get(`${TgGroupsUrl}`);
};

const postGroup = async (name, sheriff, link, info, linkDateTime, linkExpDateTime) => {
  return await axios.post(
    `${TgGroupsUrl}`,
    {
      'name': name,
      'sheriff': sheriff,
      'link': link,
      'info': info,
      'linkDateTime': linkDateTime,
      'linkExpDateTime': linkExpDateTime}
  );
};

export default {
  getGroups,
  postGroup
}


