// TgGroupsAppReducer.js
// ENL Fi Dev / RedFoxFinn
// Redux-reducer for Telegram-group listings web-application

const getInitialState = () => ({
  siteLanguage: 'fi',
  error: null
});

const appReducer = (state = getInitialState(), action) => {
  switch (action.type) {
    case 'SWITCH_LANGUAGE':
      return {...state, siteLanguage: action.siteLanguage};
    case 'ERROR_ON':
      return {...state, error: action.error};
    case 'ERROR_OFF':
      return {...state, error: null};
    default:
      return state;
  }
};

export default appReducer


