// TgGroupsAppLanguageSelection.js
// ENL Fi Dev / RedFoxFinn
// Telegram-group listings web-application language selection functionality

import React from 'react';
import { connect } from 'react-redux';
import { Container, Row, Col } from 'react-grid-system';

const mapStateToProps = (state) => {
  return {
    appState: state.appState
  };
};

function textBySiteLanguage(siteLanguage) {
  switch (siteLanguage) {
    case 'fi':
      return {
        helper: 'sivu saatavilla seuraavilla kielillä: '
      };
    case 'en':
      return {
        helper: 'site available on following languages: '
      };
    default:
      return {
        helper: 'sivu saatavilla seuraavilla kielillä: '
      };
  }
}

function languages(changeLanguage) {
  const languages = [
    {value: 'fi', language: 'suomi/finnish', country: 'fi'},
    {value: 'en', language: 'english (uk)', country: 'gb'}
  ];
  
  return languages.map(language => {
    const url = `https://www.countryflags.io/${language.country}/flat/32.png`;
    return (
      <a type='button' key={language.language} title={language.language} onClick={event => changeLanguage(event, language.value)}>
        {<img src={url} alt={language.language}/>}
      </a>
    )
  })
}

class TgGroupsAppLanguageSelection extends React.Component {
  componentDidMount() {
  };
  
  changeLanguage = (event, value) => {
    event.preventDefault();
    this.props.dispatch({type: 'SWITCH_LANGUAGE', siteLanguage: value});
  };
  
  render() {
    let text = textBySiteLanguage(this.props.appState.siteLanguage);
    return (
      <div>
        <Container fluid>
          <Row justify='around' align='center'>
            <p>{text.helper}</p>
            {languages(this.changeLanguage)}
          </Row>
        </Container>
      </div>
    )
  }
}

export default connect(mapStateToProps)(TgGroupsAppLanguageSelection)


